<template lang="pug">
v-app(light)
	v-navigation-drawer(fixed='true' :mini-variant='miniVariant' :clipped='clipped' v-model='drawer' app)

		v-toolbar(raised elevation="8")
			v-list.pa-0
				v-list-tile.white--text.blue-grey(avatar)
					v-list-tile-avatar.white--text
						i.fas.fa-sun.fa-3x
					v-list-tile-content.white--text(style="font-size: 21px")
						v-list-tile-title.dignity The Ant Irony Bloc
		v-list.with.dignity(@click.native.stop="drawer = !drawer")
			v-list-tile(v-for='(item, i) in items', :key='i', value='true', :to='item.url', active-class="active", exact)
				v-list-tile-action
					v-icon(light, v-html='item.icon')

				v-list-tile-content
					v-list-tile-title(v-text='item.title')

	v-toolbar(app fixed='true')
		v-toolbar-side-icon(@click.stop='drawer = !drawer')

		v-btn(icon, @click.stop='miniVariant = !miniVariant')
			v-icon(v-html="miniVariant ? 'chevron_right' : 'chevron_left'")

		v-btn(icon, @click.stop='clipped = !clipped')
			v-icon rowing

		v-btn(icon, @click.stop='fixed = !fixed')
			v-icon wifi
		v-icon.mx-2(large) fas fa-sun
		v-toolbar-title.dignity.ml-1(v-text='title')

		v-spacer

	v-content
		v-container(fluid)
			v-slide-y-transition(mode='out-in')
				router-view

	v-footer(:fixed='fixed', app)
		span
			v-icon(large) beach_access
			| &nbsp; Cloned in Sambodia 2018 by the Gran Bretônio et honi soit qui mal y pense
</template>

<script>
	export default {
		data () {
			return {
				clipped: false,
				drawer: false,
				fixed: false,
				items: [
					{
						icon: 'all_inclusive',
						title: 'Trinities in General',
						url: '/'
					},
					{
						icon: 'fastfood',
						title: 'De Stultibus',
						url: '/x-file'
					},
					{
						icon: 'beach_access',
						title: 'Sobre um Pobre',
						url: '/sobre-isso'
					},
					{
						icon: 'wb_sunny',
						title: 'Sobre um Nobre',
						url: '/sobre-aquilo'
					}
				],
				miniVariant: false,
				right: true,
				rightDrawer: false,
				title: 'Engenho Novo LLC'
			}
		}
	}
</script>

<style>

.primary {
	color: white;
	background-color: #607D8B;	
}

.material-icons.md-18 { font-size: 18px; }
.material-icons.md-24 { font-size: 24px; }
.material-icons.md-36 { font-size: 36px; }
.material-icons.md-48 { font-size: 48px; }
.material-icons.md-54 { font-size: 54px; }
.material-icons.md-66 { font-size: 66px; }
.material-icons.md-72 { font-size: 72px; }
.material-icons.md-84 { font-size: 84px; }
.material-icons.md-96 { font-size: 96px; }

/* Rules for using icons as black on a light background. */
.material-icons.md-dark { color: rgba(0, 0, 0, 0.54); }
.material-icons.md-dark.md-inactive { color: rgba(0, 0, 0, 0.26); }

/* Rules for using icons as white on a dark background. */
.material-icons.md-light { color: rgba(255, 255, 255, 1); }
.material-icons.md-light.md-inactive { color: rgba(255, 255, 255, 0.3); }

.hero-image {
	background-image: url("/machiine-one.jpg");
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	filter: grayscale(100%) contrast(50%);
}

.hero-text {
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: white;
}

</style>


