</template>

<script>
export default {
	name: 'HelloWorld',
	data () {
		return {
			msg: 'Bom Dia, Meia-Noite'
		}
	}
}
</script>


<style scoped>

#bg {
position: fixed; 
top: -50%; 
left: -50%; 
width: 200%; 
height: 200%;
}

#bg img {
position: absolute; 
top: 0; 
left: 0; 
right: 0; 
bottom: 0; 
margin: auto; 
min-width: 50%;
min-height: 50%;
filter: grayscale(100%) contrast(40%) opacity(110%);
}

v-container {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
h1, h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
.primary {
	blackground-color: #607D8B;
}
</style>



